import React from 'react';
import {Button, Intent, Popover, PopoverInteractionKind, Position, ButtonGroup} from '@blueprintjs/core';
import {withRouter} from 'react-router-dom';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {deletePrinter, editPrinter, loadPrinters} from '../../../../actions/PrintersActions';
import _isEqual from 'lodash/isEqual';
import {DELETE, EDIT} from "../../../../api/constants/Actions";
import _find from 'lodash/find';
import {PRINTER} from "../../../../api/constants/Entities";
import {isFormValid} from "../../../../helpers";
import {DELETE_PRINTER, EDIT_PRINTER} from "../../../../helpers/constants";
import Toaster from "../../../Common/Toaster";
import Can from "../../../../helpers/Can";
import { SETTINGS } from '../../../../api/constants/Pages';

class View extends React.Component{

    state = {
        printer: {},
        printerBuffer: {},
        printerRaw: {},
        confirmDelete: false
    };

    static getDerivedStateFromProps(nextProps, prevState){
        const {printerRaw: oldPrinterRaw} = prevState;
        const {printers, match: {params: {id}}} = nextProps;
        let printerRaw = _find(printers, {id: Number.parseInt(id, 10)}) || {};
        const printer = {...printerRaw};

        if(_isEqual(oldPrinterRaw, printerRaw))
            return null;

        return {printer, printerBuffer: {...printer}, printerRaw};
    }

    componentDidUpdate(prevProps){
        const {OPERATION_SUCCESSFUL: OLD_OPERATION_SUCCESSFUL, OPERATION_FAILED: OLD_OPERATION_FAILED} = prevProps;
        const {OPERATION_SUCCESSFUL, OPERATION_FAILED, history, authUser: {token}, loadPrinters} = this.props;

        if(!_isEqual(OLD_OPERATION_SUCCESSFUL, OPERATION_SUCCESSFUL)){
            loadPrinters(token, {});
            const printer = this.state.printer.serial.toUpperCase();

            if(OPERATION_SUCCESSFUL.action === EDIT_PRINTER){
                history.goBack();
                Toaster.show({
                    message: `${printer} Successfully Updated 😃`,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }

            if(OPERATION_SUCCESSFUL.action === DELETE_PRINTER){
                history.goBack();
                Toaster.show({
                    message: `${printer} Successfully Deleted 😃`,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }
        }

        if(!_isEqual(OLD_OPERATION_FAILED, OPERATION_FAILED)){
            const printer = this.state.printer.serial.toUpperCase();
            if(OPERATION_FAILED.action === EDIT_PRINTER){
                console.log(OPERATION_FAILED.data);
                Toaster.show({
                    message: `Could Not Update ${printer} 😞`,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
            if(OPERATION_FAILED.action === DELETE_PRINTER){
                console.log(OPERATION_FAILED.data);
                Toaster.show({
                    message: `Could Not Delete ${printer} 😞`,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
        }
    }

    _handleInputChange = (name, value) => {
        this.setState(() => {
            return {
                printer: {...this.state.printer, [name]: value}
            }
        });
    }

    _handleDeletePrinter = () => {
        this.props.deletePrinter(this.props.authUser.token, this.state.printer.id);
        this.setState({confirmDelete: false});
    }

    _handleEditPrinter = () => {
        const {authUser: {token}, editPrinter} = this.props;
        const {printer: {serial, laminator}, printerRaw: {id}} = this.state;

        let data = new FormData();

        data.append('serial', serial);
        data.append('laminator', laminator);

        editPrinter(token, id, data);
    }

    _isFormValid = () => {
        const {printer: {serial, laminator}} = this.state;

        let required = {serial, laminator};

        return isFormValid(required);
    }

    render(){
        const {history, editingPrinter, deletingPrinter, authUser} = this.props;
        const {printer: {serial, laminator}, printerBuffer, printer, confirmDelete, printerRaw} = this.state;
        const disableSaveButton = !this._isFormValid() || _isEqual(printer, printerBuffer);
        const printerNotFound = !printerRaw.id;

        const editActionAllowed = authUser.root ? true : Can.User(authUser).perform(EDIT, PRINTER, SETTINGS);
        const deleteActionAllowed = authUser.root ? true : Can.User(authUser).perform(DELETE, PRINTER, SETTINGS);

        return [
            <div className="bp3-dialog-body">

                <label className="bp3-label">
                    Serial Number
                    <input
                        value={serial || ''}
                        name={'serial'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
                <label className="bp3-label">
                    Laminator Serial Number
                    <input
                        value={laminator || ''}
                        name={'laminator'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
            </div>,
            <div className="bp3-dialog-footer">
                <div className="bp3-dialog-footer-actions">
                    {printerNotFound && (
                        <Button
                            intent={Intent.PRIMARY}
                            onClick={() => history.goBack()}
                            icon="undo"
                            text="Go Back"
                        />
                    )}

                    <ButtonGroup>
                        <Popover isOpen={confirmDelete} interactionKind={PopoverInteractionKind.CLICK} position={Position.BOTTOM}>
                            <Button
                                disabled={deletingPrinter || editingPrinter || printerNotFound || !deleteActionAllowed}
                                loading={deletingPrinter}
                                onClick={() => this.setState({confirmDelete: true})}
                                intent={Intent.DANGER}
                                text={'delete'}
                                icon="trash" />
                            <div style={{ padding: ".5rem", display: "flex", flexDirection: "column" }}>
                                <header>Are you sure?</header>
                                <div style={{ display: "flex", justifyContent: "space-evenly", marginTop: ".5rem" }}>
                                    <Button onClick={this._handleDeletePrinter} small text="yes" intent={Intent.SUCCESS}/>
                                    <Button onClick={() => this.setState({confirmDelete: false})} small text="no" intent={Intent.DANGER}/>
                                </div>
                            </div>
                        </Popover>
                    </ButtonGroup>

                    <Button
                        disabled={disableSaveButton || deletingPrinter || printerNotFound || !editActionAllowed}
                        loading={editingPrinter}
                        intent={Intent.PRIMARY}
                        onClick={this._handleEditPrinter}
                        icon="tick"
                        text="save"
                    />
                </div>
            </div>
        ];
    }

    static propTypes = {
        history: PropTypes.object.isRequired,
        authUser: PropTypes.object.isRequired,
        match: PropTypes.object.isRequired,
        OPERATION_FAILED: PropTypes.object.isRequired,
        OPERATION_SUCCESSFUL: PropTypes.object.isRequired,
        editPrinter: PropTypes.func.isRequired,
        deletePrinter: PropTypes.func.isRequired,
        loadPrinters: PropTypes.func.isRequired,
        editingPrinter: PropTypes.bool.isRequired,
        deletingPrinter: PropTypes.bool.isRequired,
        printers: PropTypes.array.isRequired,
    };
}

const mapStateToProps = (
    {authUser, OPERATION_FAILED, OPERATION_SUCCESSFUL, editingPrinter, deletingPrinter, printers}) => (
        {authUser, OPERATION_FAILED, OPERATION_SUCCESSFUL, editingPrinter, deletingPrinter, printers});
const mapDispatchToProps = dispatch => bindActionCreators({deletePrinter, editPrinter, loadPrinters}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(View));


